import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import userPhoto from '../../../assets/image.png';
import Preloader from '../../common/Preloader/Preloader';
import Contact from './Contact';
import style from './ProfileInfo.module.css';
import ProfileStatus from './ProfileStatus';

const ProfileInfo = ({ profile, ...props }) => {
  if (!profile) {
    return <Preloader />;
  }

  return (
    <div>
      <div className={style.descriptionBlock}>
        <img
          className={style.ava__img}
          src={profile.photos.large || userPhoto}
          alt='profilePhoto'
        />

        {props.params.userId ? (
          <div>
            <Link
              onClick={() => {
                props.setIdForMessage(profile.userId);
              }}
              to={'/dialogs'}
            >
              Whrite the message
            </Link>
          </div>
        ) : null}

        <ProfileData profile={profile} {...props} />
      </div>
    </div>
  );
};

const ProfileData = ({ profile, ...props }) => {
  return (
    <div>
      <div>
        <div>
          <b>FullName: </b> {profile.fullName}
        </div>
        <div>
          <b></b>
        </div>
        <div>
          <b>Status: </b>
          <ProfileStatus
            userStatus={props.userStatus}
            updateUserStatus={props.updateUserStatus}
            authorizedId={props.authorizedId}
            params={props.params}
          />
          <div className={style.description}> Click to change</div>
        </div>
        <div>
          <b>LookingForAJob: </b> {profile.lookingForAJob ? 'Yes' : 'No'}
        </div>
        <div>
          <b>Skills: </b>
          {profile.lookingForAJobDescription || 'empty'}
        </div>
        <div>
          <b>About Me: </b> {profile.aboutMe}
        </div>
      </div>
      <div className={style.contacts}>
        <b>Contacts: </b>
        {Object.keys(profile.contacts)
          .filter((key) => key !== 'vk')
          .map((key, index) => {
            return (
              <div className={style.contact}>
                <Contact
                  key={index}
                  contactTitle={key}
                  contactValue={profile.contacts[key]}
                />
              </div>
            );
          })}
      </div>
    </div>
  );
};

export default React.memo(ProfileInfo);
